import request from '@/request/index'

export async function finishedHomework(url: string, method: string) {
  return await request(url, method)
}

export async function unfinishedHomework(url: string, method: string) {
  return await request(url, method)
}

export async function expireHomework(url: string, method: string) {
  return await request(url, method)
}
