<template>
  <div class="flex w-full h-screen flex-col">

    <Head title="作业列表"></Head>
    <main class="flex-1 flex flex-col">
      <div class="navList flex justify-around h-3.5 lg:h-1.5 bg-sky-300">
        <div @click="activateChange(value.id)" class="box relative flex justify-around items-center"
          v-for="value in navList" :key="value.id">
          <span class="text-[0.2rem] lg:text-[0.15rem] font-light"
            :style="{ 'color': value.id == activateIndex ? 'black' : '#3e3e3e' }">{{
              value.title }}</span>
          <div :class="{ 'hidden': value.id !== activateIndex }"
            class="border-2 border-green-500 w-3 absolute bottom-0"></div>
        </div>
      </div>
      <div v-if="homework.length === 0" class="flex-1 flex justify-center items-center flex-col">
        <Image src="/public/image/empty.png"></Image>
        <span class="text-[0.2rem] font-light pt-1">暂无数据</span>
      </div>
      <div v-else>
        <van-cell-group>
          <van-cell v-for="value in homework" :key="value.homework" :title="value.homework"
            :value="value.time"></van-cell>
        </van-cell-group>
      </div>
    </main>
  </div>
</template>
<script setup lang='ts'>
import Head from '@/components/head.vue'
import Image from '@/components/image.vue';
import { finishedHomework as FinishedHomework, unfinishedHomework as UnfinishedHomework, expireHomework as ExpireHomework } from '@/request/homework/index'
import type { AxiosResponse } from 'axios';
interface navList {
  id: number,
  title: string
}
const navList = ref<Array<navList>>([
  {
    id: 0,
    title: '未完成',
  },
  {
    id: 1,
    title: '已完成',
  }
  , {
    id: 2,
    title: '已过期'
  }
])
const activateIndex = ref<number>(0)
watch(activateIndex, (newVal) => {
  if (newVal == 0) {
    homework.value = unfinishedHomework.value
  } else if (newVal == 1) {
    homework.value = finishedHomework.value
  } else if (newVal == 2) {
    homework.value = expireHomework.value
  }
})
const activateChange = (id: number) => {
  activateIndex.value = id
}
const unfinishedHomework = ref([])
const finishedHomework = ref([])
const expireHomework = ref([])
const homework = ref<Array<any>>([])
onMounted(() => {
  FinishedHomework('/api/finishedHomework', 'get').then((res: AxiosResponse) => {
    if (res.status === 200) {
      finishedHomework.value = res.data.data
    }
  })
  UnfinishedHomework('/api/unfinishedHomework', 'get').then((res: AxiosResponse) => {
    if (res.status === 200) {
      unfinishedHomework.value = res.data.data
      homework.value = res.data.data//初始化
    }
  })
  ExpireHomework('/api/expireHomework', 'get').then((res: AxiosResponse) => {
    if (res.status === 200) {
      expireHomework.value = res.data.data
    }
  })
})
</script>
